.app {
  min-height: 100vh;
  background-color: #1D3557;
  position: relative;
  overflow: hidden;
}

body {
  margin: 0;
  font-family: "Inter", sans-serif;
}

h2 {
  letter-spacing: 0.05em;
}

.bg-svg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 0;
  pointer-events: none;
}

.bg-svg svg {
  width: 100%;
  height: 120%;
  display: block;
  object-fit: cover;
}
.content {
  position: relative;
  z-index: 1;
  padding: 2rem;
}

.svg-bg {
  width: 100%;
  height: 100%;
  animation: float 12s ease-in-out infinite;
}

.wave {
  transform-origin: center;
}

.wave1 {
  animation: waveMove 12s ease-in-out infinite;
}

.wave2 {
  animation: waveMove 14s ease-in-out infinite reverse;
}

.wave3 {
  animation: waveMove 18s ease-in-out infinite;
}

.wave4 {
  animation: waveMove 20s ease-in-out infinite reverse;
}

@keyframes waveMove {
  0%, 100% {
    transform: translateY(0px) scaleX(1);
  }
  50% {
    transform: translateY(-30px) scaleX(1.05);
  }
}

.title {
  color: #F1FAEE;
  font-size: 4rem;
  text-align: center;
  margin-top: 4rem;
  letter-spacing: 0.2em;
  font-weight: 600;
}

.roadmap {
  margin-top: 4rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(12px);
  border-radius: 20px;
  padding: 1.5rem;
  color: #F1FAEE;
  box-shadow: 0 10px 30px rgba(0,0,0,0.3);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0,0,0,0.4);
}

.card h2 {
  margin-bottom: 0.5rem;
  color: #E5989B;
}

.card p {
  font-size: 0.9rem;
  opacity: 0.8;
}

.progress {
  margin-top: 1rem;
  height: 8px;
  background: rgba(255,255,255,0.1);
  border-radius: 10px;
  overflow: hidden;
}

.bar {
  height: 100%;
  background: linear-gradient(90deg, #E5989B, #CDB4DB);
  border-radius: 10px;
  transition: width 0.5s ease;
}